<i18n>
{
  "en": {
    "unpaid": "Unpaid order",
    "SuccessfullyReturned": "Successfully returned the battery",
    "Consumption": "Consumption",
    "Unit": "(Yuan)",
    "cost": "{count}Yuan",
    "TheLeaseTime": "The lease time",
    "time": "{0}days, {1}hours, {2}minutes.",
    "ShopAddress": "Shop address",
    "address": "1 floor, building 3, garden district.",
    "Complete": "Confirmation of payment",
    "billNum": "Order number",
    "startTime": "Start time",
    "usetimes": "Duration of use",
    "payCost": "Cost"
  },
  "zh": {
    "unpaid": "未支付订单",
    "SuccessfullyReturned": "成功归还充电宝",
    "Consumption": "消费",
    "Unit": "（元）",
    "cost": "{count}元",
    "TheLeaseTime": "租借时长",
    "time": "{0}天{1}小时{2}分钟",
    "ShopAddress": "店铺位置",
    "address": "花园小区3号楼1层",
    "Complete": "确认支付",
    "billNum": "订单编号",
    "startTime": "起始时间",
    "usetimes": "使用时长",
    "payCost": "费用"
  }
}
</i18n>

<template>
  <div class="unpaid">
    <head-top :title='$t("unpaid")' :custom-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <div class="billInfo">
        <span>{{$t("billNum")}} :</span>
        <span>{{data.no}}</span>
      </div>
      <div class="billInfo">
        <span>{{$t("startTime")}} :</span>
        <span>{{data.lent_time}}</span>
      </div>
      <div class="billInfo">
        <span>{{$t("usetimes")}} :</span>
        <span>{{$t("time", timers)}}</span>
      </div>
      <div class="billInfo">
        <span>{{$t("payCost")}} :</span>
        <span class="payed">￥{{parseFloat(data.fee / 100) }}</span>
      </div>
      <button @click="goHandle" class="btn">{{$t("Complete")}}</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import { setStorage, getStorage } from '../../utils/local'
export default {
  name: 'restituteSuccessfully',
  data () {
    return {
      timers: [0, 0, 0],
      data: {}
    }
  },

  components: {
    headTop
  },

  mounted () {
    this.data_init()
    // 计算租借时长
    this.dataInteractionResponse(data => {
      if (data.msgType === '0x5224' && data.result) {
        this.data = data.data
        console.log(this.data)
        this.getTimes()
        setStorage('billData', data.data)
      }
    })
    if (this.ISDEBUG) {
      window.dataInteractionResponse('{"msgType":"0x5224","result":true,"data":{"no":"LD20190114181143","lent_adress":"广东省广州市天河区建中路3号","lent_time":"2019-01-14 18:12:01","return_time":"2019-01-14 18:22:24","return_adress":"广东省广州市天河区建中路3号","duration":"少于1分钟","fee":120,"pay_state":1,"discount_fee":0,"balance_deduction":100,"need_pay_fee":0}}')
    }
  },

  methods: {
    ...mapActions(['sidebarSwitch']),
    data_init () {
      if (this.$route.params.data) {
        let no = this.$route.params.data.data[0].no
        console.log(no)
        this.dataInteractionRequest({
          msgType: '0x5223',
          no: no
        })
      } else {
        let data = JSON.parse(getStorage('billData'))
        if (data) {
          this.data = data
          this.getTimes()
        }
      }
    },
    goBackHander () {
      console.log(11)
      this.sidebarSwitch(false)
      this.$router.go(-1)
    },
    goHandle () {
      this.$router.push({
        name: 'orderBill',
        params: {
          item: this.data
        }
      })
    },
    getTimes () {
      let d, h, m
      let ldate = new Date(this.data.lent_time)
      console.log(ldate)
      let Rdate = new Date(this.data.return_time)
      console.log(Rdate)
      console.log(this.data)
      let times = Rdate.getTime() - ldate.getTime()
      // console.log(times)
      d = parseInt(times / 1000 / 3600 / 24)
      times = times - d * 1000 * 3600 * 24
      h = parseInt(times / 1000 / 3600)
      times = times - h * 1000 * 3600
      m = parseInt(times / 1000 / 60)
      this.timers = [d, h, m]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.unpaid{
  .fs;
  background: #FFF;
  .container{
    padding: 73px 0 0 0;
    .fs-noTop;
    .billInfo{
      display: flex;
      font-size: 27px;
      margin-bottom: 70px;
      padding-left: 52px;
      span:last-child{
        color: #878787;
        margin-left: 36px;
        align-self: center;
      }
      span:first-child{
        color: black;
        font-weight: 600;
      }
      .payed{
        color: #e43e3e !important;
      }
    }
    button{
      .wh-l(73%, 90px);
      background: @themeColor;
      border-radius: 20px;
    }
  }
}
</style>



